import { MeetingRoom } from '@im/livekit';
import { Box } from '@mui/material';
import { mainModel } from '@src/libs/mc/main/MainModel';

export default function JoinCall() {
    const urlParams = new URL(window.location.href).searchParams;
    const name = urlParams.get("code") || "test";
    const role = (urlParams.get("role") || "initiator") as 'initiator' | 'responder'; // Get role from URL
    const eventType = (urlParams.get("eventType") || "security") as 'medical' | 'security'; // Get event type from URL

    // 根据事件类型和角色确定音视频初始状态
    const getInitialMediaSettings = () => {
      if(1==1){
        return {
          audio:true,
          video:true,
          canControlMedia:true
        }
      }
        if (eventType === 'medical') {
            // 医疗呼救：发送方发送音视频，接收方初始只接收，可手动控制
            return {
                audio: role === 'initiator', // 发起者开启音频
                video: role === 'initiator', // 发起者开启视频
                canControlMedia: true // 接收方可以手动控制
            };
        } else {
            // 安全呼救：发送方发送音视频但不接收，接收方只接收
            return {
                audio: role === 'initiator', // 发起者开启音频
                video: role === 'initiator', // 发起者开启视频
                canControlMedia: role === 'responder' // 只有接收方可以控制
            };
        }
    };
    const mediaSettings = getInitialMediaSettings();

  return (
    <Box sx={{height: '100vh'}}>
        <Box>
            name:{name}, role:{role}, eventType:{eventType}
        </Box>
      <MeetingRoom
        roomName={name}
        directly={true}
        userName={`user-${Math.random().toString(36).substring(2, 8)}`}
        livekitConfig={mainModel.appConfig.livekit}
        initialAudioEnabled={mediaSettings.audio}
        initialVideoEnabled={mediaSettings.video}
        role={role}
        eventType={eventType}
        canControlMedia={mediaSettings.canControlMedia}
      />
    </Box>
  );
}
